.container {
  justify-content: center;
  margin-top: 180px;
  display: flex;
}

button {
  width: 180px;
  height: 60px;
  border: inherit;
  color: #fff;
  background-color: #0e0ee7;
  border-radius: 15px;
  outline: 1px solid #2f4f4f;
  margin-left: 50px;
  font-size: 30px;
  font-weight: bold;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 5px 5px 5px 5px #8d8d8d;
}

button:hover, button:focus {
  background-color: #ca45a9;
  transform: scale(1.1);
}

button:disabled {
  opacity: .5;
  background-color: #0e0ee7;
  transform: scale(1);
}

